/*
 * The imagetent reducer will always return an array of image's no matter what
 * You need to return something, so if there are no image's then just return an empty array
 * */
import BGImg1 from '../images/bg-1.jpg';
import BGImg2 from '../images/bg-2.jpg';
import BGImg3 from '../images/bg-3.jpg';
import BGImg4 from '../images/bg-4.jpg';
import BGImg5 from '../images/bg-5.jpg';
import BGImg6 from '../images/bg-6.jpg';
import BGImg7 from '../images/bg-7.jpg';
import BGImg8 from '../images/bg-8.jpg';
import BGImg9 from '../images/bg-9.jpg';

export default function () {
    return [
        {
            image: 1,
            src: BGImg1,
            alt: ""
        },
        {
            image: 2,
            src: BGImg2,
            alt: ""
        },
        {
            image: 3,
            src: BGImg3,
            alt: ""
        },
        {
            image: 4,
            src: BGImg4,
            alt: ""
        },
        {
            image: 5,
            src: BGImg5,
            alt: ""
        },
        {
            image: 6,
            src: BGImg6,
            alt: ""
        },
        {
            image: 7,
            src: BGImg7,
            alt: ""
        },
        {
            image: 8,
            src: BGImg8,
            alt: ""
        },
        {
            image: 9,
            src: BGImg9,
            alt: ""
        }
    ]
}



